.requestquote-wizard-module-items-line-quantity-qty{
	@extend .transaction-line-views-cell-actionable-qty;
}

.requestquote-wizard-module-items-line-quantity-qty-form{
}

.requestquote-wizard-module-items-line-quantity-container-qty {
	margin-bottom: 10px;

	.requestquote-wizard-module-items-line-quantity-label-qty {
		vertical-align: middle;
		color: $sc-color-copy-dark;
		margin-bottom:$sc-margin-lv2;
		font-weight: 600;

		@media (min-width: $screen-sm-min) {
			display: table-cell;
		}

		@media (min-width: $screen-md-min) {
			display: none
		}
	}
	.requestquote-wizard-module-items-line-quantity-input-qty {
		vertical-align: middle;
		overflow: hidden;
		padding-left: 0;

		@media (min-width: $screen-sm-min) {
			display: table-cell;
			padding-left: $sc-padding-lv4;
		}

		@media (min-width: $screen-md-min) {
			padding-left: 0;
		}
	}
	.quantity-pricing-flyout-content {
		padding: 0;

		tr {
			border-bottom: 0;
}
		th,
		.quantity-pricing-table-body-quantity,
		.quantity-pricing-table-body-price {
			display: table-cell;
			padding: $sc-padding-lv1;
		}
	}
	.quantity-pricing-flyout-head-toggle{
		font-size: 16px;
	}
}

.requestquote-wizard-module-items-line-quantity-label-qty{
	@extend .transaction-line-views-cell-actionable-label-qty;
}

.requestquote-wizard-module-items-line-quantity-input-qty{
	@extend .transaction-line-views-cell-actionable-qty;
}

.requestquote-wizard-module-items-line-quantity-amount-value{
}

.requestquote-wizard-module-items-line-quantity-title-help{
	clear: both;
	margin-top: $sc-margin-lv1;
	display: block;
	font-weight:$sc-font-weight-light;
}

.requestquote-wizard-module-items-line-quantity-add, .requestquote-wizard-module-items-line-quantity-remove{

	@extend .product-details-quantity-remove;
	display: inline-block;
	height: 35px !important;
	width: 35px !important;
	margin: 0 $sc-margin-lv1;
	background-color: #fff !important;

	&:hover {
		background-color: #dd7e43 !important;
	}

	@media (min-width: $screen-md-min) {
		display: none;
	}
}

.requestquote-wizard-module-items-line-quantity-value {
	// height: $sc-target-size-mobile;
 	padding:$sc-margin-lv2 $sc-padding-lv3 $sc-margin-lv2 $sc-padding-lv3;
 	width: 90px;
 	text-align: center;
	font-family: $sc-font-family2;
	font-size: 16px;
	font-weight: $sc-font-weight-semibold;
	background-color: $sc-color-theme-background;
	border: 0;
	-webkit-border-radius: 0 0 0 0;
	border-radius: 0 0 0 0;
	border: 1px solid #b6b6b6;
}
