.product-details-image-gallery {
	position: relative;

	.bx-wrapper img {
		//margin: auto; enable this line generates a bug in PDP image gallery (in zoom functionality).
		display: block;
	}
	.photoswipe-zoom{
		position: absolute;
		right: 0;
		top: 0;
		width: 36px;
		height: 36px;
		cursor: pointer;
		box-shadow: 0px 2px 12px rgba(0,0,0,0.08);
		-webkit-transition: all 0.45s cubic-bezier(.15,0.2,.1,1);
		border-radius: 50px;
		&:before{
			content: "";
			display: block;
			width: 10px;
			height: 10px;
			border: 2px solid #000;
			border-radius: 100%;
			position: absolute;
			top: 9px;
			left: 9px;
			box-sizing: content-box;
		}
		&:after{
			content: "";
			display: block;
			width: 2px;
			height: 8px;
			background: #000;
			border-radius: 6px;
			position: absolute;
			top: 19px;
			left: 22px;
			-webkit-transform: rotate(-45deg);
			-ms-transform: rotate(-45deg);
			transform: rotate(-45deg);
			box-sizing: content-box;
		}
	}
}

.product-details-image-gallery-container {
	display: inline-block;
	height: 250px;

	@media (min-width: $screen-sm-min) {
		height: 400px;
	}

	@media (min-width: $screen-lg-min) {
		height: 555px;
	}

	img {
		max-height: 100%;
		max-width: 100%;
		width: auto;
		height: auto;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
	}
}

.product-details-image-gallery .social-sharing-flyout-content-social-pinterest {
	display: none;
}

.product-details-image-gallery:hover .social-sharing-flyout-content-social-pinterest {
	@media (min-width: $screen-md-min) {
		display: block;
	}
}

.product-details-image-gallery-detailed-image {
	height: 250px;

	@media (min-width: $screen-sm-min) {
		height: 400px;
	}

	@media (min-width: $screen-lg-min) {
		height: 555px;
	}

	img {
		max-height: 100%;
		max-width: 100%;
		width: auto;
		height: auto;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		margin: auto;
	}

	.zoomImg {
		background-color: $sc-neutral-shade-0;
	}
}

.product-details-image-gallery-prev-icon, .product-details-image-gallery-next-icon{
	position: absolute;
	top: 40%;
	width: $sc-target-size-mobile;
	height: $sc-target-size-mobile;
	font-size: 50px;
	color: $sc-color-theme-300;
	text-align: center;
	line-height: 0.8;
	@extend .fa;
}
.product-details-image-gallery-prev-icon:focus, .product-details-image-gallery-next-icon:focus {
	outline:0;
	-webkit-box-shadow:inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
	box-shadow:inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6)
}

.product-details-image-gallery-prev-icon{
	left: 30px;
	@extend %fa-angle-left;
}

.product-details-image-gallery-next-icon{
	right:30px;
	@extend %fa-angle-right;
}
